<template>
  <div>
<div class="container" v-if="loading">
  <div class="row">
      <div class="col-md-12">
          <navbar></navbar>
      </div>
  </div>
  <div class="row" style="margin-top:10px;">
    <div class="col-md-12">
            <div class="row">
            <div class="col-md-12">
                <!-- put the tabs -->
                <el-tabs @tab-click="changedTab">
                  <el-tab-pane label="Instructions">
                      <Information></Information>
                  </el-tab-pane>

                  <el-tab-pane label="Calculator">
                      <CalculatorInterface :calculator="calculator"></CalculatorInterface>
                  </el-tab-pane>

                  <el-tab-pane label="Glossary">
                      <Glosary></Glosary>
                  </el-tab-pane>                  
                </el-tabs> 
            </div>
          </div>
    </div>
  </div>
</div>
</div>
</template>

<script>
import navbar from './navbar'
import Information from './purpose'
import Glosary from './Glosary'
import BasicInterface from './basicInterface'
import AdvancedInterface from './advancedInterface'
import CalculatorInterface from './calculatorInterface'
import calculator from '@/services/calculator'
export default {
  name: 'HelloWorld',
  components: {
    Information,
    navbar,
    Glosary,
    BasicInterface,
    AdvancedInterface,
    CalculatorInterface
  },
  mounted () {
    document.body.webkitRequestFullscreen()
    this.showLoading()
  },
  methods: {
    changedTab () {
      calculator.calculate()    
    },
    showLoading () {
      var that = this
        const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(24, 32, 40, 0.94)'
        });
        setTimeout(() => {
          that.loading = true
          loading.close();
        }, 1000);
    }
  },
  data () {
    return {
      calculator: calculator,
      loading: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

h1,h2,h3,h4,h5,h6,p, table,div, button,a {
  font-family: 'Pontano Sans', sans-serif !important;
}

.table {
  font-family: 'Pontano Sans', sans-serif !important;  
}

b {
  font-family: 'Pontano Sans', sans-serif !important;  

}


</style>



