<template>
	<div class="ui segment tall stacked">
            	<div  v-if="existResults()">
                <div class="row">
                  <div class="col-md-12">
                    <h4><i class="calendar icon"></i>ANNUAL RETURNS</h4>
                  </div>
                </div>
                <div class="row" style="margin-bottom:10px;">
                  <div class="col-md-3">
                      <b>

                          <el-tooltip class="item" effect="dark" placement="top-start">
<div slot="content">
 Select the first year you would like to begin analyzing your investment. <br> If your first year was prior to 2004, select 2004; if your first year was after 2007, select 2007. <br> Quantitative analysis requires data across a minimum of one market cycle to generate meaningful results. <br> If your investment performance began after 2007, you must input a theoretical track record <br> to best estimate how your investment would have performed going back to 2007. 


</div>
                        <a style="border-radius:0px;" class="btn-xs btn btn-primary" href="#" @click="changeFirstYear"><i class="calendar outline icon"></i> Change First year</a>
                          </el-tooltip>

                      </b>
                  </div>
                  <div class="col-md-4 offset-md-5">
                      <el-checkbox @change="calculator.calculate()" v-model="calculator.privateSecurities">Private Security</el-checkbox>
                  </div>
              </div>
              <div class="row">
            		<div class="col-md-12">
	                <table class="table">
                    <thead>
                      <tr>
                        <th style="vertical-align: middle;text-align:right;">YEAR</th>
                        <th style="vertical-align: middle;text-align:right;">S&P 500</th>
                        <th style="vertical-align: middle;text-align:right;">MSCI US REIT</th>
                        <th style="vertical-align: middle;text-align:right;"> 
                          <el-tooltip class="item" effect="dark" content="click to change de name of the investment" placement="top-end">

                          <a href="#" style="color:white; width:88%; border-radius:0px;" class="btn btn-primary btn-xs" @click="changeInvestmentName"><i class="money icon"></i> {{selectedName}}</a>
                        </el-tooltip>
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr  v-for="investment in investments">
                        <td class="year" style="text-align:center;text-align:right;">{{investment.YEAR}}</td>
                        <td style="text-align:center;text-align:right;">{{investment.SP500}}%</td>
                        <td style="text-align:center;text-align:right;">{{investment.MSCI}}%</td>
                        <td  style="text-align:center;text-align:right;">
                          <input @click="clearInput(investment)" @change="changedInput(investment)" @keyup="changedInput(investment)" maxlength="6" minlength="0" class="input" masked style="text-align:center;" v-model="investment.INVESTMENT" /> %</td>
                          
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <td style="text-align:center; font-size:12px;"><b>Geometric Mean</b></td>
                       <td style="text-align:right;"><b>
                          <i-count-up
                          :start="0"
                          :end="calculator.results.Rm"
                          :decimals="2"
                          :duration="2.5"></i-count-up>%
                       </b></td>
                        <td style="text-align:right;"><b>

                          <i-count-up
                          :start="0"
                          :end="calculator.results.pGeoMeanMSCIUSREIT"
                          :decimals="2"
                          :duration="2.5"></i-count-up>%
                       </b></td>
                        <td style="text-align:center;"><b>
                          <i-count-up
                          :start="0"
                          :end="calculator.results.Ri"
                          :decimals="2"
                          :duration="2.5"></i-count-up>%
                       </b></td>
                      </tr>

                      <tr>
                        <td style="text-align:center; font-size:12px;"><b>Growth of $10,000</b><br><small>
 01/01/{{selectedYear}} to 01/01/2017
                        </small></td>

                       <td style="text-align:center; color: #025aa5;"><b>
                          $<i-count-up
                          :start="0"
                          :end="calculator.results.SPLastYear"
                          :decimals="0"
                          :duration="2.5"></i-count-up>
                       </b></td>
                        <td style="text-align:right;color: #025aa5;"><b>
                          $<i-count-up
                          :start="0"
                          :end="calculator.results.MSCILastYear"
                          :decimals="0"
                          :duration="2.5"></i-count-up>
                       </b></td>
                        <td style="text-align:center;color: #025aa5;"><b>
                          $<i-count-up
                          :start="0"
                          :end="calculator.results.PortfolioLastYear"
                          :decimals="0"
                          :duration="2.5"></i-count-up>
                       </b></td>
                      </tr>

                    </tfoot>
                </table>



            </div>
          </div>
            </div>
            <sweet-modal overlay-theme="dark" ref="modalYear" width="20%">
                <label>First year of investment</label>
                <el-select v-model="selectedYear">
                 <el-option
                    v-for="year in years"
                      :key="year.year"
                      :value="year.year"
                      :label="year.name">
                  </el-option>
                </el-select>
                <a class="btn btn-primary" @click="changeFirstYearModel" style="color:white;margin-top:10px;" type="primary" plain>Change</a>
            </sweet-modal>

            <sweet-modal overlay-theme="dark" ref="modalName" width="30%">
                <label>Name of the investment</label>
                <el-input
                    placeholder="Name of the investment"
                    @change="changeInvestmentName"
                    v-model="selectedName">
                </el-input>
                <a class="btn btn-primary" @click="changeNameModel" style="color:white;margin-top:10px;" type="primary" plain>Change</a>
            </sweet-modal>



        </div>
</template>

<script>
import portfolio from '@/components/portfolio'
import ICountUp from 'vue-countup-v2';
import _ from 'lodash'
import {TheMask} from 'vue-the-mask'
export default {
  components: {
  	portfolio,
    ICountUp,
    TheMask
  },
  props: ['calculator'],
  name: 'InvestmentTable',
  mounted () {
  	this.investments = this.calculator.getAnnualReturns()
  	this.calculator.calculate()
  },
  methods: {
  	changeFirstYear () {
  	 this.$refs.modalYear.open()
  	},
    existResults () {
      return !_.isEmpty(this.calculator.results)
    },
    changeInvestmentName () {
      this.$refs.modalName.open()
    },
    clearInput (investment) {
      console.log(investment.INVESTMENT)
      investment.INVESTMENT = 0
    },
    changeNameModel () {
      this.calculator.selectedName = this.selectedName 
      this.$refs.modalName.close()
      document.body.style.overflow = "visible"
    },
    changeFirstYearModel() {
      this.calculator.firstYear = this.selectedYear 
      this.investments = this.calculator.getAnnualReturns()
      this.calculator.calculate()
      this.$refs.modalYear.close()
      document.body.style.overflow = "visible"
    },
    changedInput: _.debounce (function (investment) {
    
      if (investment.INVESTMENT === '') {
        return false
      }

      _.each(this.investments, function (investment) {
        if (investment.INVESTMENT === '') {
          investment.INVESTMENT = 0
        }
      })


      investment.INVESTMENT = parseFloat(investment.INVESTMENT).toFixed(2)
      console.log(investment.INVESTMENT)

      if (investment.INVESTMENT !== "") {
        this.calculator.calculate()
        this.$forceUpdate()   
        this.$notify({
          title: 'Success',
          message:  this.calculator.selectedName + ' was changed',
          type: 'success'
        });


      } else {
        investment.INVESTMENT = 0
      }
    },1000)
  },
  data () {
    return {
      selectedName: this.calculator.selectedName,
    	investments: [],
      privateSecurities: this.calculator.privateSecurities,
      selectedYear: this.calculator.firstYear,
      years: [{
        name: '2004 or earlier',
        year: '2004'
      }, {
        name: '2005',
        year: '2005'
      },{
        name: '2006',
        year: '2006'
      },{
        name: '2007 or later',
        year: '2007'
      }]
    }
  }
}
</script>

<style>
 .el-popper {
  z-index:  9999999 !important;
}
  .el-tabs__nav-wrap {
    z-index: 0 !important;
  }

.el-checkbox__input.is-checked+.el-checkbox__label {
    color: #007bff !important;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #007bff;
    border-color: #007bff;
}

.year {
  font-family: 'Cutive Mono', monospace;
  font-size: 18px;
}

input[type=email], input[type=password], input[type=search], input[type=text] {
    -webkit-appearance: textfield;
    -moz-appearance: none;
}

</style>


